      <div>
      <h5 style="margin: 0;text-align: center;">To:</h5>
      <img src="../assets/images/bank.jpeg" alt="" width="50px" height="53px" style="border-radius: 10px;">
      <h5 style="margin: 0;margin-left: 0%;text-align: center;">KEAZ BANK</h5>
            </div>
    </div>
       <div class="alerts">
        <h5 style="width: 100%;">Status</h5>
        <h5 style="width: 100%;text-align: right; color:red">Pending</h5>
       </div>
       <div class="alerts">
        <h5 style="width: 100%;">Sender</h5>
        <h5 style="width: 100%;text-align: right;" id="senderName">.........</h5>
       </div>
        <div class="alerts">
